
*{
    padding: 0;
    margin: 0;
}
ul,li{
    list-style: none;
}
/* header */
.headerwrap{
    height: 200px;
    
}
.headerwrap img{
    width: 30%;
    height: 200px;
}
.headerwrap a{
    margin: 0 20px;
    color: #fff;
}

.headerwrap1{
    position: absolute;
    width: 200px;
    background-color: #d0daf3;
    height: 590px;
}
.headerwrap1 li{
    font-size: 25px;
    color: white;
    text-align: center;
    height: 54px;
    padding-top: 10px;
}
.headerwrap1 li:hover{
    background-color:#eee;
}
.headerwrap1:link{
    text-decoration: none;
    color: white;
}
.main{
    width: 1000px;
    height: 590px;
    overflow: scroll;
    margin-left: 200px;
}

